import orderBy from '@/utils/orderBy';
import { traffic, detail, trafficExternal, externalDetail } from '@/api/traffic';
import LineChart from "./components/LineChart";
export default {
  name: 'Tab',
  components: {
    LineChart: LineChart
  },
  mixins: [orderBy],
  data: function data() {
    return {
      // expandedOrNot:true,
      gate: 'wechat',
      nop: 0,
      dates: [new Date(), new Date()],
      pickerOptions: {
        shortcuts: [{
          text: '昨天',
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
            end.setTime(end.getTime() - 3600 * 1000 * 24 * 1);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '前天',
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 2);
            end.setTime(end.getTime() - 3600 * 1000 * 24 * 2);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      tableData: '',
      tableData2: [],
      subTableData: '',
      max: 8,
      enterType: '微信',
      value1: '',
      value2: [new Date(), new Date()],
      dialogTableVisible: false,
      choose: 1
    };
  },
  created: function created() {
    this.getTraffic();
  },
  methods: {
    switchGate: function switchGate(e) {
      if (e === 'wechat') {
        this.getTraffic();
      } else {
        this.getTrafficExternal();
      }

      this.gate = e;
    },
    sortChange: function sortChange(e) {
      console.log(e);
      var orderBy;

      if (e.order === 'ascending') {
        orderBy = 'ASC';
      } else {
        orderBy = 'DESC';
      }

      this.orderBy(this.tableData, e.prop, orderBy);
    },
    getTrafficExternal: function getTrafficExternal() {
      var _this = this;

      var start = parseInt(this.dates[0] / 1000);
      var end = parseInt(this.dates[1] / 1000 + 86400 - 1);
      trafficExternal({
        start_time: start,
        end_time: end,
        nop: this.nop
      }).then(function (response) {
        _this.tableData2 = response.data;
      }).catch(function (err) {
        console.log(err);
      });
    },
    getTraffic: function getTraffic() {
      var _this2 = this;

      traffic({
        start_time: this.dates[0] / 1000,
        end_time: this.dates[1] / 1000,
        nop: this.nop
      }).then(function (response) {
        console.log(response.results);
        _this2.tableData = response.data;
      }).catch(function (err) {
        console.log(err);
      });
    },
    getDetail: function getDetail(id) {
      var _this3 = this;

      detail({
        scen: id,
        start_time: this.dates[0] / 1000,
        end_time: this.dates[1] / 1000,
        nop: this.nop
      }).then(function (res) {
        _this3.subTableData = res.data.table;
        _this3.subCharts = res.data.charts;
        _this3.dialogTableVisible = true;
      });
    },
    getExterDetail: function getExterDetail(id) {
      var _this4 = this;

      var start = parseInt(this.dates[0] / 1000);
      var end = parseInt(this.dates[1] / 1000 + 86400 - 1);
      externalDetail({
        chan: id,
        start_time: start,
        end_time: end,
        nop: this.nop
      }).then(function (res) {
        _this4.subTableData = res.data.table;
        _this4.subCharts = res.data.charts;
        _this4.dialogTableVisible = true;
      });
    }
  }
};